function Slider(){
    const slider = document.querySelector(".slider"),
    firstImg = slider.querySelectorAll("img")[0];
    arrowIcons = document.querySelectorAll(".wrapper i");
    
    let isDragStart = false, prevPageX, prevScrollLeft;
    let firstImgWidth = firstImg.clientWidth + 140; //Obtener el width de la primer imagen y agregar 14 al valor del margen

    arrowIcons.forEach(icon => {
        icon.addEventListener("click", () =>{
            //console.log(icon);
            //Si el icono de la flecha izquierda es presionado, se reduce el valor de width del scroll del slider 
            slider.scrollLeft += icon.id == "left" ? - firstImgWidth : firstImgWidth;

        })
    })

    const dragStart= (e) => {
        //Actualizando variables locales en el evento mousedown
        isDragStart = true;
        prevPageX = e.pageX;
        prevScrollLeft = slider.scrollLeft;
    }

    const dragging = (e) =>{
        //scrolling imagenes del slider a la izquierda de acuerdo a la direccion del mouse
        if(!isDragStart) return;
        e.preventDefault();
        slider.classList.add("dragging");
        let positionDiff = e.pageX - prevPageX;
        slider.scrollLeft = prevScrollLeft - positionDiff;    
    }

    const dragStop = () => {
        isDragStart = false;
        slider.classList.remove("dragging");
    }

    slider.addEventListener("mousedown", dragStart);
    slider.addEventListener("mousemove", dragging);
    slider.addEventListener("mouseup", dragStop);
}

window.addEventListener("load", Slider);