import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Curso1Vue from '../views/Capacitacion/Curso1.vue'
import Curso2Vue from '../views/Capacitacion/Curso2.vue'
import Curso3Vue from '../views/Capacitacion/Curso3.vue'
import AboutUs from '../views/AboutView.vue'
import Contacto from '../views/Contacto.vue'
import CapacitacionVue from '../views/Capacitacion.vue'
//import Curso2Vue from '../views/Capacitacion/Curso2.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/AboutUS',
    name: 'AboutUS',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    component: AboutUs
  },
  {
    path: '/Capacitacion',
    name: 'Capacitacion',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/Capacitacion.vue')
    component: CapacitacionVue
  },

  {
    path: '/Capacitacion/Curso1',
    name: 'Curso1',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/Capacitacion/Curso1.vue')
    component: Curso1Vue
  },
  {
    path: '/Capacitacion/Curso2',
    name: 'Curso2',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/Capacitacion/Curso2.vue')
    component: Curso2Vue
  },
  {
    path: '/Capacitacion/Curso3',
    name: 'Curso3',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/Capacitacion/Curso3.vue')
    component: Curso3Vue
  },

  {
    path: '/Contacto',
    name: 'Contacto',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    //component: () => import(/* webpackChunkName: "about" */ '../views/Contacto.vue')
    component: Contacto
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
